import './App.css';
import { AddPost } from './components/add-post';
import Posts from './pages/posts';
import { useDispatch } from 'react-redux';
import { useEffect } from 'react';
import { addPost } from './store';

const App = () => {

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(addPost());
  }, [dispatch]);

  return (
    <div className="App">
      <AddPost />
      <Posts />
    </div>
  );
}

export default App;
